import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class FetchdataService {
data:any=[];
  constructor() { 
    fetch("http://jsonplaceholder.typicode.com/posts/")
    .then(res=>res.json())
    .then(data=>this.data.push(data))
    .catch(err=>console.log(err))
  }
}
